import { Component, Inject, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { Prize } from '../../../shared/christmas-data';

@Component({
  selector: 'app-dialog-open-box',
  templateUrl: './dialog-open-box.component.html',
  styleUrls: ['./dialog-open-box.component.css']
})
export class DialogOpenBoxComponent implements OnInit {

  public showImage = true;
  public prize: Prize;
  public day: number;
  public isOpenGift;
  public canOpenGift;
  public isCurrentDayBox: boolean;
  constructor(public dialogRef: MatDialogRef<DialogOpenBoxComponent>, @Inject(MAT_DIALOG_DATA) public data: any) { }

  ngOnInit(): void {
    this.prize = this.data.isWinningBox;
    this.isOpenGift = !this.data.isOpenedGift;
    this.isCurrentDayBox = this.data.isCurrentDayBox;
    this.canOpenGift = this.data.canOpenGift;
    setTimeout(() => {
      this.showImage = false;
      }, 4500);
  }

  close(): void {
    this.dialogRef.close();
  }

}
