import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { ChristmasData, User } from './christmas-data';

@Injectable({
  providedIn: 'root'
})
export class GiftManagerService {
  public users: User[];
  public christmasData: ChristmasData;
  private _currentUserEmail = '';
  constructor(private http: HttpClient) { }

  getChristmasData(): Observable<any> {
    return this.http.get<ChristmasData>('assets/redraw_data.json')
        .pipe(map((res: any) => {
          const decryptDate = atob( res.data);
          return JSON.parse(decryptDate);
        }));
  }
  getCurrentUser(): User {
    return this.christmasData.users.filter(user => user.email === this._currentUserEmail)[0];
  }

  get currentUserEmail(): string {
    return this._currentUserEmail;
  }

  set currentUserEmail(value: string) {
    this._currentUserEmail = value;
  }
}
