import { Component, ComponentFactoryResolver, OnInit, ViewChild, ViewContainerRef } from '@angular/core';
import * as _ from 'lodash';
import { BoxComponent } from './box/box.component';
import { BoxPosition } from './box/boxPosition';
import { CalendarDirective } from './calendar.directive';

@Component({
  selector: 'app-calendar',
  templateUrl: './calendar.component.html',
  styleUrls: ['./calendar.component.scss']
})
export class CalendarComponent implements OnInit {

  public screenViewContainerRef: ViewContainerRef;
  public startXPosition: number;
  public startYPosition: number;

  constructor(private resolver: ComponentFactoryResolver) { }
  @ViewChild(CalendarDirective, { static: true }) public calendarDirective: CalendarDirective;


  ngOnInit(): void {
    this.generate_grid();
    this.screenViewContainerRef = this.calendarDirective.viewContainerRef;
    const componentFactory = this.resolver.resolveComponentFactory(BoxComponent);
    const grid = this.generate_grid();
    for (const position of grid) {
      const componentRef = this.screenViewContainerRef.createComponent(componentFactory);
      componentRef.instance.boxPosition = position;
    }
  }
  generate_grid(): BoxPosition[] {
    const positions = [];
    let days: number[] = Array.from(Array(26).keys());
    days = days.filter(d => d !== 0);
    const screenWidth = window.innerWidth;
    const sliceWidth = screenWidth / 6;
    const screenHeight = window.innerHeight;
    const sliceHeight = screenHeight / 6;
    for (let col = 0; col < 5; col++) {
      for (let row = 0; row < 5; row++) {
        const colPosition = sliceWidth * col + sliceWidth / 1.1;
        const rowPosition = sliceHeight * row + sliceHeight / 1.4;
        const day = days[Math.floor(Math.random() * days.length)];
        const position = new BoxPosition(colPosition, rowPosition, day);
        days = days.filter(d => d !== day);
        positions.push(position);
      }
    }
    return _.shuffle(positions);
  }

}
