import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot } from '@angular/router';
import { GiftManagerService } from './gift-manager.service';

@Injectable({
  providedIn: 'root'
})
export class CalendarGuard implements CanActivate{
  constructor(private router: Router, private giftManagerService: GiftManagerService) {
  }

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): boolean {
    if (this.giftManagerService.currentUserEmail === '') {
      this.router.navigate(['/login']);
    }

    return true;

  }
}
