import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve } from '@angular/router';
import { Observable, of } from 'rxjs';
import { map } from 'rxjs/operators';
import { GiftManagerService } from './gift-manager.service';
import { ChristmasData } from './christmas-data';

@Injectable({
  providedIn: 'root'
})
export class DataResolver implements Resolve<ChristmasData>{

  constructor(private giftManagerService: GiftManagerService) { }

  resolve(route: ActivatedRouteSnapshot): Observable<ChristmasData> {
    if (this.giftManagerService.christmasData) {
      return of(this.giftManagerService.christmasData);
    }else {
      return this.giftManagerService.getChristmasData().pipe(
        map((res: ChristmasData) => {
          this.giftManagerService.christmasData = res;
          return res;
        })
      );
    }
  }
}
